.main {
  min-height: 100vh;
  width: 100%;
  padding: 0 5%;
  background-image: url(/reading-trainer/static/media/ezgif.com-gif-maker.5c4d8052.webp);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  overflow-y: scroll;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.img {
  width: 5rem;
  height: 5rem;
  position: absolute;
  top: 2rem;
  right: 5%;
  cursor: pointer;
  font-size: 5rem;
  color: #00b353;
  background-color: rgb(82, 82, 82);
  border-radius: 50%;
  transition: all 0.4s;
}
.img:hover {
  color: #fa3e19;
  background-color: rgb(59, 59, 59);
}
.img:active {
  transform: scale(0.7);
}
.btn {
  width: 5rem;
  height: 5rem;
  position: absolute;
  top: 2rem;
  left: 5%;
  cursor: pointer;
  font-size: 5rem;
  border-radius: 50%;
  transition: all 0.4s;
}
.btn:active {
  transform: scale(0.7);
}
.btnStart {
  font-size: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 2rem;
  color: #40b1f3;
  background-color: rgb(82, 82, 82);
}
.btnStop {
  top: 8rem;
  color: #f34040;
  background-color: rgb(82, 82, 82);
}
.btnStart:hover {
  color: #ffb830;
}
.btnStop:hover {
  color: #00b353;
}

